"use client";

import { useState } from "react";
import { checkExternalParticipation, ParticipationStatus } from "@/actions/contest-checker.action";
import { Search, Loader2, CheckCircle2, XCircle, AlertCircle, ExternalLink } from "lucide-react";

export default function ExternalContestVerificationPage() {
  const [platform, setPlatform] = useState<"CodeForces" | "LeetCode" | "CodeChef">("CodeForces");
  const [contestId, setContestId] = useState("");
  const [institutionId, setInstitutionId] = useState("");
  const [classroomId, setClassroomId] = useState("");
  const [isLoading, setIsLoading] = useState(false);
  const [results, setResults] = useState<ParticipationStatus[] | null>(null);
  const [error, setError] = useState("");

  const handleCheck = async () => {
    if (!contestId) {
       setError("Please enter a contest ID/Slug/Code");
       return;
    }
    setError("");
    setIsLoading(true);
    setResults(null);

    const res = await checkExternalParticipation(platform, contestId, {
        institutionId: institutionId || undefined,
        classroomId: classroomId || undefined
    });

    if (res.success) {
        setResults(res.results!);
    } else {
        setError(res.error || "Failed to check participation");
    }
    setIsLoading(false);
  };

  return (
    <div className="space-y-6 max-w-7xl mx-auto">
      <div>
        <h1 className="text-2xl font-bold bg-clip-text text-transparent bg-gradient-to-r from-gray-900 to-gray-600 dark:from-white dark:to-gray-400">
          External Contest Verification
        </h1>
        <p className="text-gray-500 dark:text-gray-400 mt-1">
          Verify student participation in contests hosted on supported external platforms.
        </p>
      </div>

      <div className="bg-white dark:bg-[#141414] border border-gray-200 dark:border-[#262626] rounded-xl p-6 shadow-sm">
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4 items-end">

          <div className="space-y-2">
            <label className="text-sm font-medium text-gray-700 dark:text-gray-300">Platform</label>
            <select
              value={platform}
              onChange={(e) => setPlatform(e.target.value as any)}
              className="w-full h-10 px-3 rounded-lg border border-gray-200 dark:border-[#333] bg-white dark:bg-[#1a1a1a] text-sm focus:outline-none focus:ring-2 focus:ring-orange-500/20"
            >
              <option value="CodeForces">CodeForces</option>
              <option value="LeetCode">LeetCode</option>
              <option value="CodeChef">CodeChef</option>
            </select>
          </div>

          <div className="space-y-2">
            <label className="text-sm font-medium text-gray-700 dark:text-gray-300">
               {platform === "CodeForces" ? "Contest ID (e.g. 1950)" :
                platform === "LeetCode" ? "Contest Slug (e.g. weekly-contest-390)" :
                "Contest Code (e.g. START128)"}
            </label>
            <input
              type="text"
              value={contestId}
              onChange={(e) => setContestId(e.target.value)}
              placeholder={
                platform === "CodeForces" ? "1950" :
                platform === "LeetCode" ? "weekly-contest-390" :
                "START128"
              }
              className="w-full h-10 px-3 rounded-lg border border-gray-200 dark:border-[#333] bg-white dark:bg-[#1a1a1a] text-sm focus:outline-none focus:ring-2 focus:ring-orange-500/20"
            />
          </div>

          {/* Optional Filters */}
          {/* Currently simple text inputs, could be dropdowns fetching actual data later */}
          <div className="space-y-2">
            <label className="text-sm font-medium text-gray-700 dark:text-gray-300">Institution ID (Optional)</label>
             <input
              type="text"
              value={institutionId}
              onChange={(e) => setInstitutionId(e.target.value)}
              placeholder="Filter by Institution"
              className="w-full h-10 px-3 rounded-lg border border-gray-200 dark:border-[#333] bg-white dark:bg-[#1a1a1a] text-sm focus:outline-none focus:ring-2 focus:ring-orange-500/20"
            />
          </div>

          <div className="space-y-2">
            <label className="text-sm font-medium text-gray-700 dark:text-gray-300">Classroom ID (Optional)</label>
             <input
              type="text"
              value={classroomId}
              onChange={(e) => setClassroomId(e.target.value)}
              placeholder="Filter by Classroom"
              className="w-full h-10 px-3 rounded-lg border border-gray-200 dark:border-[#333] bg-white dark:bg-[#1a1a1a] text-sm focus:outline-none focus:ring-2 focus:ring-orange-500/20"
            />
          </div>

        </div>

        <div className="mt-6 flex justify-end">
            <button
                onClick={handleCheck}
                disabled={isLoading}
                className="bg-orange-600 hover:bg-orange-700 text-white px-4 py-2 rounded-lg text-sm font-medium transition-colors flex items-center gap-2 disabled:opacity-50"
            >
                {isLoading ? <Loader2 className="w-4 h-4 animate-spin" /> : <Search className="w-4 h-4" />}
                Check Participation
            </button>
        </div>

        {error && (
            <div className="mt-4 p-3 bg-red-50 dark:bg-red-900/10 text-red-600 dark:text-red-400 text-sm rounded-lg flex items-center gap-2">
                <AlertCircle className="w-4 h-4" />
                {error}
            </div>
        )}

      </div>

      {results && (
        <div className="bg-white dark:bg-[#141414] border border-gray-200 dark:border-[#262626] rounded-xl overflow-hidden shadow-sm">
            <div className="px-6 py-4 border-b border-gray-200 dark:border-[#262626] bg-gray-50/50 dark:bg-[#1a1a1a]/50 flex justify-between items-center">
                <h2 className="font-semibold text-gray-900 dark:text-white">Results ({results.length} Students)</h2>
                <div className="flex gap-4 text-sm">
                    <span className="flex items-center gap-1 text-green-600 dark:text-green-500">
                        <CheckCircle2 className="w-4 h-4" />
                        {results.filter(r => r.participated).length} Participated
                    </span>
                     <span className="flex items-center gap-1 text-red-600 dark:text-red-500">
                        <XCircle className="w-4 h-4" />
                        {results.filter(r => !r.participated).length} Missing
                    </span>
                </div>
            </div>

            <div className="overflow-x-auto">
                <table className="w-full text-sm text-left">
                    <thead className="bg-gray-50 dark:bg-[#1a1a1a] text-gray-500 dark:text-gray-400 font-medium border-b border-gray-200 dark:border-[#262626]">
                        <tr>
                            <th className="px-6 py-3">Student</th>
                            <th className="px-6 py-3">Handle</th>
                            <th className="px-6 py-3">Status</th>
                            <th className="px-6 py-3">Rank</th>
                            <th className="px-6 py-3">Score / Time</th>
                            <th className="px-6 py-3">Details</th>
                        </tr>
                    </thead>
                    <tbody className="divide-y divide-gray-200 dark:divide-[#262626]">
                        {results.map((r) => (
                            <tr key={r.userId} className="hover:bg-gray-50 dark:hover:bg-[#1a1a1a]/50 transition-colors">
                                <td className="px-6 py-3 font-medium text-gray-900 dark:text-white">{r.name}</td>
                                <td className="px-6 py-3 text-gray-500 dark:text-gray-400 font-mono">
                                    {r.handle ? (
                                        <a href={
                                            platform === "CodeForces" ? `https://codeforces.com/profile/${r.handle}` :
                                            platform === "LeetCode" ? `https://leetcode.com/u/${r.handle}` :
                                            `https://www.codechef.com/users/${r.handle}`
                                        } target="_blank" rel="noopener noreferrer" className="hover:text-orange-500 flex items-center gap-1">
                                            {r.handle}
                                            <ExternalLink className="w-3 h-3" />
                                        </a>
                                    ) : (
                                        <span className="text-gray-400 italic">Not linked</span>
                                    )}
                                </td>
                                <td className="px-6 py-3">
                                    {r.error ? (
                                         <span className="inline-flex items-center gap-1 px-2.5 py-0.5 rounded-full text-xs font-medium bg-yellow-100 text-yellow-800 dark:bg-yellow-900/30 dark:text-yellow-500">
                                            Error
                                        </span>
                                    ) : r.participated ? (
                                        <span className="inline-flex items-center gap-1 px-2.5 py-0.5 rounded-full text-xs font-medium bg-green-100 text-green-800 dark:bg-green-900/30 dark:text-green-500">
                                            Participated
                                        </span>
                                    ) : (
                                        <span className="inline-flex items-center gap-1 px-2.5 py-0.5 rounded-full text-xs font-medium bg-red-100 text-red-800 dark:bg-red-900/30 dark:text-red-500">
                                            Absent
                                        </span>
                                    )}
                                </td>
                                <td className="px-6 py-3 text-gray-600 dark:text-gray-400">
                                    {r.rank ? `#${r.rank}` : "-"}
                                </td>
                                <td className="px-6 py-3 text-gray-600 dark:text-gray-400">
                                    {r.score !== undefined ? r.score :
                                     r.details?.finishTime ? `${Math.floor(r.details.finishTime / 60)}m ${r.details.finishTime % 60}s` : "-"}
                                </td>
                                <td className="px-6 py-3 text-xs text-gray-500 dark:text-gray-400 max-w-xs truncate">
                                    {r.error ? r.error : JSON.stringify(r.details || {})}
                                </td>
                            </tr>
                        ))}
                    </tbody>
                </table>
            </div>
        </div>
      )}
    </div>
  );
}
